@import url('https://fonts.googleapis.com/css2?family=Karla');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.2/css/fontawesome.min.css');

$colorBase: #277963CC;
$colorNota: #e6db7dcc;
$colorSuperLight: lighten($colorBase, 50%);
$colorLight: lighten($colorBase, 20%);
$colorHover: darken($colorBase, 15%);
$colorDark: darken($colorBase, 25%);
$colorMenuBg: adjust-hue($colorBase, -15deg);
$colorLogoBrillo: lighten($colorBase, 30%);
$colorSupFont: #72083B;
$colorSupBg: #FFFFFF;
$fontFamily: 'gotham';

:export {
    colorBase: $colorBase;
    colorNota: $colorNota;
    colorLight: $colorLight;
    colorHover: $colorHover;
    colorDark: $colorDark;
    colorMenuBg: $colorMenuBg;
    colorLogoBrillo: $colorLogoBrillo;
    colorSupFont: $colorSupFont;
    colorSupBg: $colorSupBg;
    fontFamily: $fontFamily;
}