@import './resources/Variables.scss';

@font-face {
	font-family: $fontFamily;
	src: url('resources/'+$fontFamily+'.ttf');
	src: url('resources/'+$fontFamily+'.ttf') format("truetype");
	font-display: auto;
}

h1 {
	margin: 0;
	padding: 0;
	font-weight: normal;
	display: inline;
}

.logo-img {
	height: 40px;
	width: 40px;
	margin-top: 0px;
}

.logo-txt {
	font-family: $fontFamily;
	font-size: 32px;
	padding: 10px 10px 10px 0px;
	vertical-align: middle;
	color: #d8d8d8;
}

.logo-animation {
	animation-duration: 3s;
	animation-name: logo;
	animation-iteration-count: infinite;
}

@keyframes logo {
	0% {
		color: #d8d8d8;
		text-shadow: 0;
	}
	40% {
		color: white;
		text-shadow: #FFF 0px 0px 5px, $colorLogoBrillo 0px 0px 10px, $colorLogoBrillo 0px 0px 15px, $colorLogoBrillo 0px 0px 20px;
	}
	60% {
		color: white;
		text-shadow: #FFF 0px 0px 5px, $colorLogoBrillo 0px 0px 10px, $colorLogoBrillo 0px 0px 15px, $colorLogoBrillo 0px 0px 20px;
	}
	100% {
		color: #d8d8d8;
		text-shadow: 0;
	}
}

.live {
	font-size: 12px;
	font-family: Karla;
	margin-left: 3px;
}

.cargando {
	color: #bfbfbf;
	font-weight: bold;
}

.texto-online {
	color: #f2ffd4;
	font-weight: bold;
}

.punto-online {
	animation-duration: 1s;
	animation-name: slidein;
	animation-iteration-count: infinite;
	width: 8px;
	height: 8px;
	border-radius: 2em;
	margin: 5px 4px 0px 0px;
}

.texto-offline {
	color: #bfbfbf;
	font-weight: bold;
}

.punto-offline {
	width: 8px;
	height: 8px;
	border-radius: 2em;
	background: #bfbfbf;
	margin: 5px 4px 0px -2px;
}

@keyframes slidein {
	20% {
		background: rgba(255, 0, 0, 0);
	}

	80% {
		background: rgba(255, 0, 0, 1);
	}

	100% {
		background: rgba(255, 0, 0, 0);
	}
}

.banner {
	width: 100%;
	height: 70px;
	margin: 0.7vh 0vh;;
	padding: 10px 0px;
}

