@import './components/resources/Variables.scss';

html {
	min-height: 100%;
}

hr {
	border-top: 3px solid $colorBase !important;
}

::placeholder {
	color: $colorSuperLight !important;
}

select {
	background-color: $colorDark !important;
	color: $colorSuperLight !important;
	border-color: $colorBase !important;
	height: 2em;
}

input {
	background-color: $colorDark !important;
	color: $colorSuperLight !important;
	border-color: $colorBase !important;
}

input:not(:placeholder-shown) {
	background-color: $colorDark !important;
	color: $colorSuperLight !important;
}

input:placeholder-shown {
	background-color: $colorDark !important;
	color: $colorSuperLight !important;
}

.custom-file {
	height: auto !important;
}

a {
	text-decoration: none !important;
	color: white !important;
	cursor: pointer;
	&:hover {
		color: #cecece !important;
	}
}

.nav-pills .pill-1 .nav-link {
	&:not(.active) {
		background-color: $colorBase !important;
	}
	background-color: $colorHover !important;
}

.nav-pills .pill-2 .nav-link {
	&:not(.active) {
		background-color: #E65A5AE6 !important;
	}
	background-color: #B33C3CE6 !important;
}

.error-form {
	color: #d34444;
	text-shadow: 1px 1px rgb(0, 0, 0);
}

.success-form {
	color: #4bb153;
	text-shadow: 1px 1px rgb(0, 0, 0);
}  

.btn-1 {
    &:hover, &:visited {
        color: white !important;
        background-color: $colorHover !important;
    }
    &:focus {
        color: white !important;
        background-color: $colorLight !important;
    }
    &:active, &:not(.active) {
        color: white !important;
        background-color: $colorBase !important;
    }
}

.btn-2 {
    &:hover, &:visited {
        color: white !important;
        background-color: #B33C3CE6 !important;
    }
    &:focus {
        color: white !important;
        background-color: #E65A5AE6 !important;
    }
    &:active, &:not(.active) {
        color: white !important;
        background-color: #E65A5AE6 !important;
    }
}

.container {
	height: calc(100vh);
	padding-bottom: 60px;
}

.titulo-h4 {
	background-color: $colorBase;
	border-radius: 0.2em;
	padding: 6px 10px;
	font-family: Arial, Helvetica, sans-serif;
}

.enlace {
	cursor: pointer;
	font-size: 100%;
	font-family: inherit;
	border: none;
	padding: 0;
	background: none;
	color: inherit;
	padding: 0;
	outline: inherit;
}